@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.pool_flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .pool_search_input {
    width: 300px;
  }
}

.pool_btn_line {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.stake_switch_label {
  font-size: 14px;
  font-weight: 400;
  color: $white;
  font-family: PingFangSC-Regular, PingFang SC;
  margin-right: 10px;
}
.pool_setting {
  background: $purple;
  border-radius: 15px;
  padding: 6px 16px 6px 48px;
  position: relative;
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $white;
  img {
    position: absolute;
    left: 0;
    top: -7px;
  }
  &:link,
  &:visited {
    color: $white;
  }
}

.pool_margin {
  margin-left: 10px;
}

@include mobile-only {
  .pool_flex {
    display: block;
    .pool_search_input {
      width: 100%;
    }
  }
  .pool_btn_line {
    justify-content: space-between;
    &:nth-child(1) {
      margin-bottom: 15px;
    }
  }

  .pool_margin {
    margin-left: 10px;
    margin-bottom: 16px;
  }
}
